.layout {
  padding: var(--standart-section-top-padding) var(--standart-side-padding);
  background-color: var(--bg);
}

.layout__wrap {
  display: grid;
  grid-template-areas: "info imageone" "imagetwo data";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(2, min-content);
  gap: 20px;
}
@media (max-width: 900px) {
  .layout__wrap {
    grid-template-areas: "info" "imageone" "data" "imagetwo";
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
  }
}

.layout__info-wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 20px;
}

.layout__title {
  grid-area: title;
  color: var(--text-primary);
}

.layout__slogan {
  align-self: start;
  grid-area: slogan;
  text-align: start;
  color: var(--text-primary);
}

.layout__info {
  grid-area: info;
  color: var(--text-primary);
}

.layout__list {
  display: grid;
  grid-area: data;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 30px;
}

.layout__list-item {
  position: relative;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr max-content 1fr;
  padding-left: 40px;
  gap: 40px;
}
.layout__list-item::before {
  content: "";
  position: absolute;
  top: 50%;
  height: 2px;
  background-color: var(--main-color);
  transform: translateY(-50%);
}
@media (max-width: 600px) {
  .layout__list-item::before {
    display: none;
  }
}
.layout__list-item:first-child::before {
  right: 0;
  grid-column: 3/-1;
  width: 100%;
}
.layout__list-item:first-child > .layout__list-title {
  grid-column: 2/3;
}
.layout__list-item:last-child::before {
  right: 0;
  grid-column: 2/-1;
  width: 100%;
}
.layout__list-item:last-child > .layout__list-title {
  grid-column: 1/2;
}
.layout__list-item:nth-child(2)::before {
  right: 0;
  grid-column: 1/2;
  width: 100%;
}
.layout__list-item:nth-child(2) > .layout__list-title {
  grid-column: 3/3;
}
@media (max-width: 600px) {
  .layout__list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-left: 0;
    width: 100%;
  }
}

.layout__list-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: var(--basci-font);
  font-weight: 500;
  font-size: 30px;
  color: var(--text-primary);
}
.layout__list-title > span {
  display: flex;
  font-family: var(--basci-font);
  font-weight: 500;
  font-size: 70px;
  white-space: nowrap;
  color: var(--main-color);
}
@media (max-width: 600px) {
  .layout__list-title {
    font-size: 20px;
  }
  .layout__list-title > span {
    font-size: 50px;
  }
}

.layout__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.layout__image:first-child {
  grid-area: imageone;
}
.layout__image:last-child {
  grid-area: imagetwo;
}
@media (max-width: 900px) {
  .layout__image {
    max-height: 200px;
  }
}

