  background-color: var(--grey-bg);
}

.inside__wrap {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.inside__titles {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, min-content);
  gap: var(--section-titles-block-margin);
}

.inside__title {
  color: var(--text-primary);
