  height: min-content;
}

.house-inside__image {
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.house-inside__floorplan {
  display: grid;
  grid-row-gap: 20px;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
}

.house-inside__property {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
@media (max-width: 600px) {
  .house-inside__property {
    justify-items: center;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, 1fr);
  }
}

.house-inside__property-title {
  grid-row: 1;
  font-weight: 500;
  font-size: 20px;
